'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Textarea } from '@/components/ui/textarea'
import { Loader2, Calendar, Target, Sparkles } from 'lucide-react'
import ContentCalendarDisplay from '@/components/ui/ContentCalendarDisplay'

interface CalendarData {
  month: string
  year: number
  dailyPosts: Array<{
    date: string
    theme: string
    postIdea: string
    caption: string
    hashtags: string[]
    suggestedVisual: string
    bestPostingTime: string
  }>
  promotionalPosts: Array<{
    title: string
    description: string
    keyFeatures: string[]
    callToAction: string
    suggestedDates: string[]
  }>
  trendingHashtags: string[]
  contentRepurposingIdeas: string[]
  analyticsInsights: {
    bestPostingTimes: string[]
    topPerformingContentTypes: string[]
    engagementTips: string[]
  }
}

export default function ContentCalendarPage() {
  const [niche, setNiche] = useState('')
  const [targetAudience, setTargetAudience] = useState('')
  const [isLoading, setIsLoading] = useState(false)
  const [calendarData, setCalendarData] = useState<CalendarData | null>(null)
  const [error, setError] = useState('')

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!niche.trim() || !targetAudience.trim()) {
      setError('Please fill in both niche and target audience fields.')
      return
    }

    setIsLoading(true)
    setError('')
    setCalendarData(null)

    try {
      const response = await fetch('/api/generate-calendar', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          niche: niche.trim(),
          targetAudience: targetAudience.trim(),
        }),
      })

      if (!response.ok) {
        const errorData = await response.json()
        throw new Error(errorData.error || 'Failed to generate calendar')
      }

      const data = await response.json()
      setCalendarData(data)
    } catch (err) {
      setError(err instanceof Error ? err.message : 'An unexpected error occurred')
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-slate-100 p-4">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-slate-900 mb-4 flex items-center justify-center gap-3">
            <Calendar className="h-10 w-10 text-blue-600" />
            AI Content Calendar Generator
          </h1>
          <p className="text-lg text-slate-600 max-w-2xl mx-auto">
            Generate a complete monthly content calendar with daily post ideas, captions, hashtags, and strategic insights tailored to your brand.
          </p>
        </div>

        {/* Input Form */}
        <Card className="mb-8 shadow-lg border-0 bg-white/80 backdrop-blur-sm">
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Target className="h-5 w-5 text-blue-600" />
              Tell Us About Your Brand
            </CardTitle>
            <CardDescription>
              Provide your niche and target audience to generate personalized content recommendations.
            </CardDescription>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="grid md:grid-cols-2 gap-6">
                <div className="space-y-2">
                  <Label htmlFor="niche" className="text-sm font-medium text-slate-700">
                    Your Niche/Industry
                  </Label>
                  <Input
                    id="niche"
                    type="text"
                    placeholder="e.g., Fitness, Tech, Food, Fashion, Business"
                    value={niche}
                    onChange={(e) => setNiche(e.target.value)}
                    className="h-12"
                    disabled={isLoading}
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="targetAudience" className="text-sm font-medium text-slate-700">
                    Target Audience
                  </Label>
                  <Input
                    id="targetAudience"
                    type="text"
                    placeholder="e.g., Young professionals, Parents, Students"
                    value={targetAudience}
                    onChange={(e) => setTargetAudience(e.target.value)}
                    className="h-12"
                    disabled={isLoading}
                  />
                </div>
              </div>

              {error && (
                <div className="p-4 bg-red-50 border border-red-200 rounded-lg">
                  <p className="text-red-700 text-sm">{error}</p>
                </div>
              )}

              <Button
                type="submit"
                disabled={isLoading}
                className="w-full h-12 bg-blue-600 hover:bg-blue-700 text-white font-medium"
              >
                {isLoading ? (
                  <>
                    <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                    Generating Your Content Calendar...
                  </>
                ) : (
                  <>
                    <Sparkles className="mr-2 h-4 w-4" />
                    Generate Content Calendar
                  </>
                )}
              </Button>
            </form>
          </CardContent>
        </Card>

        {/* Results */}
        {calendarData && (
          <ContentCalendarDisplay data={calendarData} />
        )}
      </div>
    </div>
  )
}
